.content {
text-align: center;
font-family:Arial, Helvetica, sans-serif;
width:100%;
margin-top: 200px;
}
.topContent {
    display: flex;
    justify-content: space-around;
    margin-bottom: 30px;
}
.topLeftContent {
    display: flex;
    flex-direction: column;
    text-align: left;
    gap: 10px;
}
.distance {
    color: blue;
    font-size: larger;
    font-weight: 400;
}
.price {
    color: green;
    font-size: larger;
    font-weight: 700;
}
.button {
 margin-left: 220px;
 height: 60px;
 text-align: right;
 width: 230px;
 border-radius: 8px;
 border-color: whitesmoke;
 background-color: rgb(50, 50, 230);
 font-size: larger;
 color: white;
}
.img {
    width: 100%;
    height: auto;
    border: solid 5px white;
}
.midContent {
    display: grid;
    grid-template-columns: 500px 500px 500px;
    grid-template-rows: 400px 400px;
    align-items: stretch;
    justify-content: center;
}
.botContent {
    display: flex;
    margin-top: 30px;
}
.botLeftContent {
    display: flex;
    flex-direction: column;
    text-align: start;
    width: 45%;
    margin-left: 200px;
    gap: 10px;
}
.botRightContent {
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 20px;
    width: 25%;
    height: 300px;
    background-color: rgb(223, 242, 235);
    margin-left: 180px;
}
.p {
    margin-left: 20px;
    font-size: large;
}
.button2 {
    margin-left: 11px;
    height: 60px;
    text-align: center;
    width: 450px;
    border-radius: 8px;
    border-color: whitesmoke;
    background-color: rgb(50, 50, 230);
    font-size: larger;
    color: white;
}
.pri {
    text-align: center;
    margin-left: 20px;
    font-size: 28px;
    font-weight: 700;
}
.description {
    font-size: larger;
}